import React, { useEffect, useState, useRef } from "react";
import { useNavigate } from "react-router-dom"; // Hook para navegao
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../../styles/Playlist.css";
import { jwtDecode } from "jwt-decode"; // Decodificao do token JWT
import { io } from "socket.io-client"; // Biblioteca Socket.IO

const SOCKET_URL = process.env.REACT_APP_SOCKET_URL;

const MyRequests = () => {
  const [musicas, setMusicas] = useState([]); // Msicas requisitadas
  const [loading, setLoading] = useState(true); // Controle de carregamento
  const [searchTerm, setSearchTerm] = useState(""); // Estado para o campo de pesquisa
  const socketRef = useRef(null); // Referncia para o socket
  const navigate = useNavigate(); // Hook para navegao

  // Funo para buscar msicas requisitadas
  const fetchMusicas = async () => {
    try {
      const token = localStorage.getItem("token"); // Token JWT salvo
      if (!token) throw new Error("Token no encontrado.");

      // Decodifica o token JWT para acessar os dados no payload
      const decodedToken = jwtDecode(token);
      const userId = decodedToken.userId;
      if (!userId) throw new Error("user_id no encontrado no token.");

      // Faz a requisio para buscar as msicas
      const response = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/getrequests?user_id=${userId}`,
        {
          method: "GET",
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (!response.ok) throw new Error("Erro ao carregar msicas.");

      const data = await response.json();
      const twoWeeksAgo = new Date();
      twoWeeksAgo.setDate(twoWeeksAgo.getDate() - 14);

      const allMusicas = data.flatMap((req) =>
        req.musicas
          .filter((musica) => new Date(req.data_requisicao) >= twoWeeksAgo)
          .map((musica) => ({
            ...musica,
            venueNome: req.venue.nome, // Adiciona o nome da venue
            dataRequisicao: req.data_requisicao, // Adiciona a data da requisio
            dataResposta: musica.data_resposta || null, // Adiciona a data da resposta para ordenao
            played: !!musica.played, // Converte o campo played para booleano
            comentario: musica.comentario || "", // Adiciona o comentrio da venue
          }))
      );

      // Ordena as msicas pela dataResposta mais recente
      allMusicas.sort((a, b) => {
        const dateA = new Date(a.dataResposta || a.dataRequisicao);
        const dateB = new Date(b.dataResposta || b.dataRequisicao);
        return dateB - dateA; // Ordena da mais recente para a mais antiga
      });

      setMusicas(allMusicas);
    } catch (error) {
      console.error(error);
      toast.error("Erro ao carregar msicas.");
    } finally {
      setLoading(false);
    }
  };

  // Funo para formatar a data da requisio
  const formatarData = (data) => {
    const date = new Date(data);
    return date.toLocaleDateString("pt-PT", {
      year: "numeric",
      month: "long",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  // Funo para eliminar uma msica
  const handleDeleteMusic = async (musicId) => {
    try {
      const token = localStorage.getItem("token");
      const response = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/delete-music`,
        {
          method: "DELETE",
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ music_id: musicId }),
        }
      );

      if (!response.ok) throw new Error("Erro ao eliminar msica.");

      // Atualiza a lista de msicas aps a eliminao
      setMusicas((prevMusicas) =>
        prevMusicas.filter((musica) => musica.id !== musicId)
      );

      toast.success("Msica eliminada com sucesso.");
    } catch (error) {
      console.error(error);
      toast.error("Erro ao eliminar msica.");
    }
  };

  // Conectar-se ao WebSocket para receber atualizaes em tempo real
  useEffect(() => {
    fetchMusicas();

    // Conectar ao WebSocket
    socketRef.current = io(SOCKET_URL);

    socketRef.current.on("connect", () => {
      console.log("Conectado ao servidor:", socketRef.current.id);
      const token = localStorage.getItem("token");
      const decodedToken = jwtDecode(token);
      const userId = decodedToken.userId;
      const roomId = `user-${userId}`;

      socketRef.current.emit("join-room", roomId);
      console.log(`Entrando na sala: ${roomId}`);
    });

    // Ouvir por atualizaes de msica (aceita ou rejeitada)
    socketRef.current.on("music-action-response", (response) => {
      console.log("Resposta recebida:", response);
      const { message, music_id, status_text, data_resposta, comentario } =
        response;

      // Atualiza o estado da msica conforme a resposta do servidor
      setMusicas((prevMusicas) => {
        const updatedMusicas = prevMusicas.map((musica) =>
          musica.id === music_id
            ? {
                ...musica,
                status_text,
                dataResposta: data_resposta,
                comentario,
              }
            : musica
        );

        // Reordena aps atualizao
        updatedMusicas.sort((a, b) => {
          const dateA = new Date(a.dataResposta || a.dataRequisicao);
          const dateB = new Date(b.dataResposta || b.dataRequisicao);
          return dateB - dateA;
        });

        return updatedMusicas;
      });

      // Exibe uma notificao
      toast.success(message, { position: "top-right", autoClose: 5000 });
    });

    // Ouvir por notificaes de msica tocada
    socketRef.current.on("music-played", (response) => {
      console.log("Notificao de msica tocada recebida:", response);
      const { message, music_id } = response;

      // Atualiza o estado da msica para tocada
      setMusicas((prevMusicas) => {
        const updatedMusicas = prevMusicas.map((musica) =>
          musica.id === music_id ? { ...musica, played: true } : musica
        );

        return updatedMusicas;
      });

      // Exibe uma notificao
      toast.success(message, { position: "top-right", autoClose: 5000 });
    });

    return () => {
      if (socketRef.current) {
        socketRef.current.disconnect();
        console.log("Socket desconectado.");
      }
    };
  }, []);

  // Funo para lidar com o boto de voltar
  const handleBackClick = () => {
    // Navega para a pgina inicial
    navigate("/home");
  };

  // Funo para traduzir o estado
  const traduzirEstado = (estado) => {
    switch (estado.toLowerCase()) {
      case "approve":
        return "Aprovado";
      case "deny":
        return "Rejeitado";
      case "pending":
        return "Pendente";
      default:
        return "Desconhecido";
    }
  };

  // Filtra as msicas com base na pesquisa
  const filteredMusicas = musicas.filter((musica) =>
    musica.nome.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="playlist-container">
      <ToastContainer />

      {/* Boto de voltar */}
      <button className="back-button" onClick={handleBackClick}>
         Voltar
      </button>

      <h2>Minhas Requisies</h2>

      {/* Barra de pesquisa */}
      <input
        type="text"
        placeholder="Pesquisar msica..."
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        className="search-bar"
      />

      {loading ? (
        <p>Carregando...</p>
      ) : filteredMusicas.length > 0 ? (
        <div className="playlist-grid">
          {filteredMusicas.map((musica) => (
            <div
              key={musica.id}
              className={`music-item ${musica.played ? "played" : ""}`}
            >
              <img
                src={musica.imagem || "https://via.placeholder.com/150"}
                alt={musica.nome}
                className="music-thumbnail"
              />
              <div className="music-details">
                <p className="music-title">{musica.nome}</p>
                <p className="venue-name">Venue: {musica.venueNome}</p>
                <p className="request-date">
                  <strong>Requisitado em:</strong>{" "}
                  {formatarData(musica.dataRequisicao)}
                </p>
                {musica.comentario && (
                  <p className="music-comment">
                    <strong>Comentrio:</strong> {musica.comentario}
                  </p>
                )}
                <div className="music-status-container">
                  <p
                    className={`music-status ${musica.status_text.toLowerCase()}`}
                  >
                    {traduzirEstado(musica.status_text)}
                  </p>
                  {musica.status_text.toLowerCase() !== "deny" &&
                    (musica.played ? (
                      <i className="fas fa-music music-played-icon"></i>
                    ) : (
                      <i className="fas fa-clock music-not-played-icon"></i>
                    ))}
                  {(musica.status_text.toLowerCase() === "approve" ||
                    musica.status_text.toLowerCase() === "deny") && (
                    <i
                      className="fas fa-trash-alt music-delete-icon"
                      onClick={() => handleDeleteMusic(musica.id)}
                    ></i>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <p>Nenhuma msica encontrada!</p>
      )}
    </div>
  );
};

export default MyRequests;
